.footer {
  --footer-height: 200px;

  background-image: url(../images/footer.jpg);
  background-size: cover;
  background-position: right;
  height: var(--footer-height);

  @include onTablet {
    --footer-height: 480px;
  }

  @include onDesktop {
    --footer-height: 480px;
  }
}
